/* global window, chrome, document */

const webspeedStorage = require('storage/webspeed');
function getSubrequests() {
  return window.performance.getEntries().reduce(
    function (result, current) {
      result.total += 1;
      if (current.duration < 10) {
        // https://developer.mozilla.org/en-US/docs/Web/API/PerformanceResourceTiming
        // We currently don't have a reliable way to determine whether
        // subqueries are cached or not (even queries that are cached end up with
        // non zero time entries)
        // Instead, we use a heuristic of < 10ms to determine the number of subqueries
        // that are cached.

        // TODO: consider using https://developer.chrome.com/extensions/devtools_network
        // instaed of https://developer.mozilla.org/en-US/docs/Web/API/PerformanceResourceTiming
        result.fast += 1;
      }
      return result;
    }, {
      fast: 0,
      total: 0
    }
  );
}



function getWebspeed(httpResponse, adBlock, _ip) {
  const timing = window.performance.timing;
  const domCompleteDuration = ((timing.domComplete - timing.navigationStart) * 0.001).toFixed(2);

  return {
    url: window.location.origin,
    webpage_ip: _ip,
    httpResponse: httpResponse,
    version: window.navigator.appVersion.match(/.*Chrome\/([0-9\.]+)/)[1],
    timing: timing,
    domCompleteDuration: Number(domCompleteDuration),
    subrequests: getSubrequests(),
    adBlock: adBlock
  };
}

document.onreadystatechange = function ()  {

  let testAd = document.createElement('div');
  testAd.id = 'testAd';
  testAd.innerHTML = '&nbsp';
  testAd.id = 'adsbox';
  document.body.appendChild(testAd);

};

window.addEventListener('load', () =>  {

  chrome.runtime.sendMessage({greeting: 'get_ip', t: window.location.origin}, (_ip) => {
    let blocked = (document.getElementById('adsbox').offsetHeight > 0) ? false : true;
    document.getElementById('adsbox').remove();
    chrome.storage.local.get('options', function(res) {
      if (res.options && res.options.webspeed) {
        chrome.storage.local.get('httpResponse', (response) => {
          webspeedStorage.save(getWebspeed(response.httpResponse, blocked, _ip));
          chrome.storage.local.remove('httpResponse');
        });

      }
    });
    return;
  });

}, false);
